.App {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.container {
    width: 80%;
    height: 80%;
    background-color: #fff;
    box-shadow: 0px 10px 60px 20px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    overflow: hidden;
}

.logo {
    height: 45px;
    margin-bottom: 30px;
}

.loader {
    position: relative;
    width: 100%;
    height: 1%;
    background-color: #FFCCBC;
}

@keyframes move {
    0% {
        left: 0%
    }

    100% {
        left: 100%;
    }
}

.loader::before {
    position: absolute;
    content: "";
    width: 30%;
    height: 100%;
    background-color: #FF3D00;
    animation: move 1s infinite linear;
}

.text-wrapper {
    width: 100%;
    height: 100%;
    border: none;
    font-size: 20px;
    padding: 10px;
}

.image-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}

.text-wrapper:focus {
    outline: none;
}


/* width */
::-webkit-scrollbar {
    width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 27px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}

.custom-file-input {
    outline: none;
}

.custom-file-input::-webkit-file-upload-button {
    visibility: hidden;
}

.custom-file-input::before {
    content: 'Upload Your Image';
    display: inline-block;
    background: #fff;
    border: 1px solid #999;
    border-radius: 3px;
    padding: 5px 8px;
    outline: none;
    white-space: nowrap;
 
    cursor: pointer;
    text-shadow: 1px 1px #fff;
    font-weight: 700;
    font-size: 10pt;
    padding: 20px 50px;
    border-radius: 27px;
}

.custom-file-input:hover::before {
    border-color: black;
}